<template>
  <el-container
   
    :element-loading-text="loadingText"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <!-- 头部 -->
    <el-header class="n2-electronic-header">
      <N2Header>
        <Screen slot="left" :isClick="false" :lists="placeList" />
        <Screen
          style="margin-left: 0.08rem"
          slot="center"
          :isClick="true"
          :lists="timeList"
          @isActive="isActive"
        />
      </N2Header>
    </el-header>

    <!-- 主体内容 -->
    <el-main class="n2-electronic-main">
      <ErrorUnderly class="top-underly" :underlyTitle="underlyTitle[0]">
        <MonochromeBar
          :xDatas="dataList.Floor"
          :yDatas="dataList.SumNow"
          slot="center"
          class="bar-size"
        />
        <ErrorFloorTable
          :tableHead="tableHeadList1"
          :lists="tableList1"
          class="table-size"
          slot="footer"
        />
      </ErrorUnderly>
      <ErrorUnderly class="bottom-underly" :underlyTitle="underlyTitle[1]">
        <MonochromeBar
          :left="'20%'"
          :barWidth="'70'"
          :xDatas="['流量超标', '低于设置下限']"
          :yDatas="chartData2"
          slot="center"
          class="bar-size"
        />
        <ErrorFloorTable
          :tableHead="tableHeadList2"
          :lists="tableList2"
          class="bottom-table-size"
          slot="footer"
        />
      </ErrorUnderly>
    </el-main>
  </el-container>
</template>

<script>
//header 组件
import N2Header from "@/components/n2Header";
//screen
import Screen from "@/components/screen/screen";
import ErrorUnderly from "@/components/errorAnalysis/errorUnderly";
import ErrorFloorTable from "@/components/errorAnalysis/errorFloorTable";

//echarts 图
import MonochromeBar from "@/components/charts/monochromeBar";

//网络数据
import { getErrorAnalysis } from "@/api/errorAnalysis/errorAnalysis";
export default {
  name: "ErrorAnalysis",
  components: {
    N2Header,
    Screen,
    ErrorUnderly,
    MonochromeBar,
    ErrorFloorTable,
  },
  data() {
    return {
      placeList: ["郑州", "观澜", "太原"],
      timeList: ["日", "周", "月", "年"],
      dataList: {},
      tableList1: [],
      tableHeadList1: [],
      tableList2: [],
      tableHeadList2: [],
      chartData2: [],
      underlyTitle: ["当日异常楼层排行", "当日异常原因排行"],
      time: "Day",
      loading: true,
      loadingText: "拼命加载中",
      intervalValue: "",
    };
  },
  created() {
    this.getErrorAnalysis(this.time);
    this.intervalValue = setTimeout(() => {
      this.getErrorAnalysis(this.time);
    }, 15 * 1000);
  },
  beforeDestroy() {
    if (this.intervalValue != "") clearInterval(this.intervalValue);
  },
  methods: {
    //获取数据
    getErrorAnalysis(time) {
      this.loadingText = "拼命加载中";
      getErrorAnalysis(time)
        .then((res) => {
          //清空原数据
          this.dataList = {};
          this.tableList1 = [];
          this.tableHeadList1 = [];
          this.tableList2 = [];
          (this.tableHeadList2 = []), (this.chartData2 = []);
          this.underlyTitle = [];
          switch (time) {
            case "Day":
              this.tableHeadList1 = [
                "排行",
                "楼层",
                "当日异常次数",
                "上日异常次数",
              ];
              this.tableHeadList2 = [
                "排行",
                "异常原因",
                "当日异常次数",
                "上日异常次数",
              ];
              this.underlyTitle = ["当日异常楼层排行", "当日异常原因排行"];
              break;
            case "Week":
              this.tableHeadList1 = [
                "排行",
                "楼层",
                "当周异常次数",
                "上周异常次数",
              ];
              this.tableHeadList2 = [
                "排行",
                "异常原因",
                "当周异常次数",
                "上周异常次数",
              ];
              this.underlyTitle = ["当周异常楼层排行", "当周异常原因排行"];
              break;
            case "Month":
              this.tableHeadList1 = [
                "排行",
                "楼层",
                "当月异常次数",
                "上月异常次数",
              ];
              this.tableHeadList2 = [
                "排行",
                "异常原因",
                "当月异常次数",
                "上月异常次数",
              ];
              this.underlyTitle = ["当月异常楼层排行", "当月异常原因排行"];
              break;
            case "Year":
              this.tableHeadList1 = [
                "排行",
                "楼层",
                "当年异常次数",
                "上年异常次数",
              ];
              this.tableHeadList2 = [
                "排行",
                "异常原因",
                "当年异常次数",
                "上年异常次数",
              ];
              this.underlyTitle = ["当年异常楼层排行", "当年异常原因排行"];
              break;
            default:
              break;
          }

          if (res.Status == "0") {
            this.dataList = res.Data.N2FlowMessage;
            let FloorList = this.dataList.Floor;
            let SumNowList = this.dataList.SumNow;
            let SumLastList = this.dataList.SumLast;
            let tableList1 = []; //第一个table数据
            FloorList.forEach((element) => {
              tableList1.push([element]);
            });
            SumNowList.forEach((data, index) => {
              tableList1[index].push(data);
            });
            SumLastList.forEach((data, index) => {
              tableList1[index].push(data);
            });
            tableList1.sort((a, b) => {
              return b[1] - a[1];
            });
            this.tableList1 = tableList1;
            this.tableList2.push(
              [
                "流量超标",
                this.dataList.AbnormalUpNow[0],
                this.dataList.AbnormalUpLast[0],
              ],
              [
                "低于设置下限",
                this.dataList.AbnormalDownNow[0],
                this.dataList.AbnormalDownLast[0],
              ]
            );
            this.chartData2.push(
              this.dataList.AbnormalUpNow[0],
              this.dataList.AbnormalDownNow[0]
            );
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
          this.loadingText = "网络异常! 请尝试刷新......";
        });
    },

    //选择时间
    isActive(index) {
      this.loading = true;
      let time = "";
      switch (this.timeList[index]) {
        case "日":
          time = "Day";
          break;
        case "周":
          time = "Week";
          break;
        case "月":
          time = "Month";
          break;
        case "年":
          time = "Year";
          break;
        default:
          break;
      }
      this.time = time;
      this.getErrorAnalysis(time);
    },
  },
};
</script>

<style lang="scss" scoped>
.n2-electronic-header {
  height: 0.4rem !important;
  margin-bottom: 0.05rem;
  padding: 0;
}
.n2-electronic-main {
  padding: 0;
  .top-underly {
    width: 7.32rem;
    height: 6.13rem;
  }
  .bar-size {
    width: 7.32rem;
    height: 2.32rem;
  }
  .table-size {
    height: 3.43rem;
  }
  .bottom-underly {
    margin-top: 0.08rem;
    width: 7.32rem;
    height: 3.73rem;
    .bottom-table-size {
      height: 1.03rem;
    }
  }
}
</style>